import React, { Component } from 'react';

class Rates extends Component {
    constructor(props){
        super(props)
        this.state = {
            currency: "GBP"
        }
    }

    render(){
        return(
            <div>
                <h4>
                    One {this.props.base} 
                    <select>
                        {Object.keys(this.props.rates.rates).map((key, index) => {
                            return(
                                <option value={this.props.rates.rates[key]}>{key}</option>
                            )
                        })}
                    </select>
                </h4>
            </div>
        )
    }
}

export default Rates;